<template>
    <div>
        <div class="header">
            <menubar/>
        </div>
        <div class="container">
            <form class="col-md-6" @submit.prevent="payOrder">
            <table class="table">
            <thead>
                <th>品名</th>
                <th>數量</th>
                <th>單價</th>
            </thead>
            <tbody>
                <tr v-for="item in order.products" :key="item.id">
                <td class="align-middle">{{ item.product.title }}</td>
                <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                <td class="align-middle text-right">{{ item.final_total }}</td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                <td colspan="2" class="text-right">總計</td>
                <td class="text-right">{{ order.total }}</td>
                </tr>
            </tfoot>
            </table>

            <table class="table">
            <tbody>
                <tr>
                <th width="100">Email</th>
                <td>{{ order.user.email }}</td>
                </tr>
                <tr>
                <th>姓名</th>
                <td>{{ order.user.name }}</td>
                </tr>
                <tr>
                <th>收件人電話</th>
                <td>{{ order.user.tel }}</td>
                </tr>
                <tr>
                <th>收件人地址</th>
                <td>{{ order.user.address }}</td>
                </tr>
                <tr>
                <th>付款狀態</th>
                <td>
                    <span v-if="!order.is_paid">尚未付款</span>
                    <span v-else class="text-success">付款完成</span>
                </td>
                </tr>
            </tbody>
            </table>
            <div class="text-right" v-if="order.is_paid === false">
            <button class="btn btn-danger">確認付款去</button>
            </div>
        </form>
        <div class="text-right" v-if="order.is_paid === true">
            <router-link class="btn btn-danger" to="/index">回首頁</router-link>
        </div>
        </div>
        
    </div>
</template>
<style lang="scss" scoped>
    .container{
        margin-top: 64px;
    }
    .col-md-6{
        margin: 0 auto;
    }
</style>
<script>
import menubar from './topmenu'
export default {
    components:{menubar},
    data(){
        return{
            OrderId:'',
            order:{
                user:{}
            },
        }
    },
    methods:{
        getOrder(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.APIID}/order/${vm.OrderId}`
            this.$http.get(url).then((response) => {                
                console.log(response.data);
                vm.order = response.data.order;
            });
        },
        payOrder(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.APIID}/pay/${vm.OrderId}`
            this.$http.post(url).then((response) => {                
                console.log(response.data);
                vm.getOrder()
            });
        }
    },
    created(){
        this.OrderId = this.$route.params.order_id
        this.getOrder()
    }
}
</script>


